.Testimonial h2 {
  margin: 15px 0 0 0;
}

.Testimonial .left {
  width: 40%;
  padding: 35px;
  margin: 20px;
}

.Testimonial .left .img {
  /* height: 300px; */
  overflow: hidden;
  transition: 0.5s;
}
.Testimonial img {
  width: 100%;
  border-radius: 10px;
  transition: 0.5s;
}
.Testimonial .left .img:hover img {
  transform: scale(1.2);
}

.Testimonial .right {
  width: 60%;
  /* padding: 0 35px 0 35px; */
  /* margin: 0 20px 0 20px; */
}
.Testimonial .quote i {
  font-size: 150px;
  opacity: 0.1;
}

.Testimonial .content {
  padding: 50px;
}
.Testimonial .content h1 {
  font-weight: 500;
}
.Testimonial .content h3 {
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 30px;
}
.Testimonial .slide_button {
  position: absolute;
  top: 30px;
  right: 20px;
}
.Testimonial .slide_button .prev_btn {
  margin-right: 30px;
}

/*-----Testimonial slide-----------*/
.Testimonial .slide {
  max-width: 90%;
  margin: auto;
  height: 77vh;
  position: relative;
  display: flex;
  overflow: hidden;
}
article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s linear;
}
article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide {
  transform: translateX(-100%);
}
article.nextSlide {
  transform: translateX(100%);
}
/*-----Testimonial slide-----------*/

@media (max-width: 768px) {
  article {
    flex-direction: column;
    padding: 20px;
  }
  .Testimonial .left {
    width: 100%;
    display: flex;
    margin: 0;
    margin-top: 15%;
  }

