  }
  .Testimonial img {
    width: 100%;
    border-radius: 10px;
    transition: 0.5s;
  }
  .Testimonial .left .img:hover img {
    transform: scale(1.2);
  } */

  .Testimonial .right {
    width: 100%;
    margin: 0;
    padding: 0 0 0 0px;
  }
